.clients-box {
  background: #00000035;
  border: #04375a 1px solid;
  border-radius: 30px;
  padding: 30px 40px;
  color: #fff;
  max-width: 500px;
  margin: 50px 100px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  margin-right: 110px;
}

.clients-box ul {
  list-style: none;
  padding: 0;
}

.clients-box li {
  margin: 12px 0px;
  width: 100%;
  font-size: 18px;
  color: #fff;
}

.clients-box .highlight {
  color: #ffd700;
}

.mic-container {
  width: 50%;
}
.mic-img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 30px;
  margin-left: 90px;
}

.fa-microphone {
  color: #fff !important;
  margin-left: 10px;
}

/* Tablet and smaller desktop adjustments */
@media (max-width: 1340px) {
  .clients-box {
    width: 100%;
    margin: 30px auto; /* Center and reduce margins */
    padding: 25px 30px;
  }

  .clients-box li {
    font-size: 16px;
  }

  .mic-img {
    max-width: 80%;
  }
}
@media (max-width: 992px) {
  .clients-box {
    width: 100%;
    margin: 30px auto; /* Center and reduce margins */
    padding: 25px 30px;
  }

  .clients-box li {
    font-size: 14px;
  }

  .mic-img {
    max-width: 80%;
  }
}

/* Mobile adjustments */

@media (max-width: 768px) {
  .customers-title {
    font-size: 2rem;
  }

  .clients-box {
    margin: 20px auto; /* Further reduce margins */
    padding: 20px;
    max-width: 50%; /* Full width on small screens */
  }

  .clients-box li {
    font-size: 0.7rem;
  }
  .mic-container {
    width: 70%;
  }
  .mic-img {
    max-width: 70%;
    border-radius: 20px; /* Slightly reduce for mobile */
    margin: auto;
  }
}
