.header-section {
  font-family: "Tajawal", sans-serif; /* Arabic-friendly font */
}

.btn-outline-light {
  border: 1px solid #fff;
  color: #fff;
  transition: 0.3s;
}

.btn-outline-light:hover {
  background-color: #f9c600;
  color: #000;
  border-color: #f9c600;
}

.img-home {
  width: 300px;
}

.editor {
  width: 140px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

h6 {
  margin-top: 5px;
  font-weight: 600;
  font-size: larger;
}

@media (max-width: 768px) {
  .editor {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
  }
}
